<template>
  <div id="walletData">
    <common-header title="零钱明细"></common-header>
    <div class="walletData__list">
      <div class="data" v-for="item in 5">
        <div class="data__left">
          <div class="data__left__info">充值</div>
          <div class="data__left__date">2017-12-12   12:32:00</div>
        </div>
        <div class="data__right">+ 5.0</div>
      </div>
    </div>
  </div>  
</template>

<script>
  export default {
    data() {
      return {
        accountLogs: []
      }
    },

    created() {
      this.getAccountDetailList();
    },

    methods: {
      getAccountDetailList() {
        this.$Api.getAccountDetailList(1).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
            this.accountLogs = res.q.accountLogs;
          }
        })
      }
    }
  }
</script>


<style lang="scss">
  #walletData {
    .walletData__list {
      .data {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 0.4rem;
        height: 1.7067rem;
        border-bottom: 1px solid #eee;
        
        .data__left {
          display: inline-block;
          .data__left__info {
            padding-bottom: 0.1rem; 
            font-size: 0.4rem;
            color: #333;
          }

          .data__left__date {
            font-size: 0.373rem;
            color: #666;
          }
        }

        .data__right {
          font-size: 0.533rem;
          color: #de7fef;
        }
      }
    }
  }
</style>




